<template>
    <v-container fluid grid-list-md>
        <v-layout wrap>

            <template v-for="user in users">
                <v-flex xs12 sm4 md3 lg2>
                    <explicador :user="user"></explicador>
                </v-flex>
            </template>

            <template v-for="user in users">
                <v-flex xs12 sm4 md3 lg2>
                    <explicador :user="user"></explicador>
                </v-flex>
            </template>


        </v-layout>
        <explicador-help-dialog></explicador-help-dialog>
    </v-container>
</template>

<script>

    export default {
        beforeCreate () {
            this.$Progress.start()
        },
        mounted () {

            if (this.users.length > 0)
            {
                this.$Progress.finish()
            }

        },
        data () {
            return {
            }
        },
        computed: {
            users () {
                return this.$store.getters.getUsers
            }
        },
        watch: {
            users (value) {
                this.$Progress.finish()
            }
        },
        methods: {
//          Alterna do menu principal para a pagina e menus do perfil
            goToProfile () {
                this.$store.dispatch('togleHomePage',false)
            }
        }
    }
</script>
<style scoped>
    .explicador-bar-fixed {
        position: fixed;
        z-index: 1;
        left: 0;
        width: 100%;
        /*left: 0;*/
    }
</style>


