import { store } from "../../store/index";

export default (to, from, next) => {

    // Quanto a seguranca, melhor tratar no componente profile.


    // Buscando info para ver se os menus eestao a ser mostrados ou nao
    const isSideMenusShowing = store.getters.isSideMenusShowing

    if (!isSideMenusShowing) { //Mostra os menus se nao estiver mostrando nem nao estiver no login nem registo

        store.dispatch('togleSideMenus',true)

    }

    store.dispatch('togleHomePage',false) //para dizer que ja nao estamos na pagina home, vai definir o novo menu como do perfil (melhorar isto)


    next()


}


