<template>
    <div>
        <v-list dense>
            <template>
                <!--Select da disciplina encima no drawer esquerdo-->
                <v-layout justify-center fill-height v-if="isHomePage">
                    <v-flex xs11>
                        <v-select
                                v-bind:items="tipos"
                                v-model="e2"
                                label="Select"
                                single-line
                                auto
                                prepend-icon="map"
                                hide-details
                        ></v-select>
                    </v-flex>
                </v-layout>

                <!--Campo de pesquisa encima no drawer esquerdo-->
                <v-layout v-if="isHomePage">
                    <v-flex>
                        <v-text-field
                                light
                                solo
                                v-model="searchedData"
                                prepend-icon="search"
                                :placeholder="'Pesquisar ' + comp_searchedData"
                                class="elevation-0"
                                style="max-width: 400px; min-width: 128px"
                        ></v-text-field>
                    </v-flex>
                </v-layout>

                <!--Todo: Primeiro divider que poe a linha de divisao-->
                <v-divider v-if="isHomePage"></v-divider>

                <!--Todo: Layout com a lista das discipplinas que sao mostradas, falta colocar o scroll-->
                <div v-for="item in menuItens">
                    <!--Todo: Segundo divider que poe a linha de divisao-->
                    <v-divider v-if="item.aditional"></v-divider>
                    <template>
                        <v-list-tile @click="">
                            <v-list-tile-action>
                                <v-icon>{{ item.icon }}</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>
                                    {{ item.text }}
                                </v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>
                    </template>
                </div>
                <div v-if="isUserAuth">
                    <sugestion-dialog></sugestion-dialog>
                </div>
            </template>

            <!--Todo: Segundo divider que poe a linha de divisao-->
            <v-divider></v-divider>
        </v-list>
    </div>
</template>

<script>
    export default {
        data: () => ({
            right: true,
            offsetTop: 0,
            searchedData: '',
            e2: ''
        }),
        computed: {
            comp_searchedData ()  {
                return this.e2;
            },
            tipos() {
                return this.$store.getters.getTipos
            },
            menuItens () {
                return this.$store.getters.getMenuOptions
            },
            isHomePage () {
                return this.$store.getters.isHomePage
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
        },
        methods: {

        }
    }
</script>


